import { useEffect, useState } from "react";
import { useUpdate } from "./useUpdate";
import defaultRecord from "test/defaultRecords.json";

export type RecordItem = {
  tagIds: string[];
  note: string;
  category: "+" | "-";
  amount: string;
  createAt: string; // IOS 8601
};

type newRecordItem = Omit<RecordItem, "createAt">;

//  Money 
export const useRecords = () => {
  const [moneyRecords, setMoneyRecords] = useState<RecordItem[]>([]);

  //   records 
  useEffect(() => {
    setMoneyRecords(
      JSON.parse(
        window.localStorage.getItem("records") || JSON.stringify(defaultRecord))
    );
  }, []);

  //   records ,  localStorage
  useUpdate(() => {
    window.localStorage.setItem("records", JSON.stringify(moneyRecords));
  }, [moneyRecords]);

  const addRecord = (newRecord: newRecordItem) => {
    if (newRecord.amount === "0") {
      alert("");
      return false;
    }
    if (newRecord.tagIds.length === 0) {
      alert("");
      return false;
    }
    const record = { ...newRecord, createAt: new Date().toISOString() }; //  IOS 8601
    setMoneyRecords([...moneyRecords, record]);
    return true;
  };
  return { moneyRecords, addRecord };
};
