import { createId } from "lib/createId";
import { useEffect, useState } from "react";
import { useUpdate } from "./useUpdate";

export type TagType = {
  id: string;
  name: string;
};

//  Hooks
export const useTags = () => {
  const [tags, setTags] = useState<TagType[]>([]);
  //   
  useEffect(() => {
    let localTags = JSON.parse(window.localStorage.getItem("tags") || "[]");
    if (localTags.length === 0) {
      localTags = [
        { id: createId(), name: "" },
        { id: createId(), name: "" },
        { id: createId(), name: "" },
        { id: createId(), name: "" },
        { id: createId(), name: "" },
      ];
    }
    setTags(localTags);
  }, []);

  //  tags  localStorage
  //   tags  undefined => [] 
  useUpdate(() => {
    window.localStorage.setItem("tags", JSON.stringify(tags));
  }, [tags]);

  const onAddTag = () => {
    const tagName = window.prompt("");
    if (tagName === "") return;
    if (tagName !== null) {
      setTags([...tags, { id: createId(), name: tagName }]);
    }
  };
  const findTag = (tagId: string) => {
    let tag = tags.find((tag) => tag.id === tagId);
    return tag?.name;
  };
  const updatetag = (tagId: string, newTagName: string) => {
    setTags(
      tags.map((tag) =>
        tag.id === tagId ? { id: tagId, name: newTagName } : tag
      )
    );
  };
  const deleteTag = (tagId: string) => {
    setTags(tags.filter((tag) => tag.id !== tagId));
  };
  return { tags, setTags, onAddTag, findTag, updatetag, deleteTag };
};
