import styled from "styled-components";

//  <Input label="" type="text" placeholder="" value={tagName}></Input>
// 1. ts 
//  &
// input = React.InputHTMLAttributes<HTMLInputElement>
type Props = {
  label: string;
} & React.InputHTMLAttributes<HTMLInputElement>;

const Label = styled.label`
  margin-top: 10px;
  display: flex;
  align-items: center;
  > span {
    margin-right: 16px;
    white-space: nowrap;
  }
  > input {
    border: 1px solid #000 !important;
    border-radius: 8px;
    display: block;
    padding-left: 5px;
    width: 100%;
    height: 44px;
    background: none;
    border: none;
  }
`;

// 2.  rest 
export const Input: React.FC<Props> = (props) => {
  const { label, children, ...rest } = props;
  return (
    <Label>
      <span>{label}</span>
      <input {...rest} />
    </Label>
  );
};
